/* src/App.css
   Improved, mobile-first styling for Animal World
   - consistent image sizes
   - readable cards with info
   - centered search
   - beautiful region picker
   - responsive grid
*/

/* --- Reset / base --- */
* { box-sizing: border-box; }
html,body,#root { height: 100%; }
body {
  margin: 0;
  font-family: "Inter", "Segoe UI", Roboto, system-ui, -apple-system, "Helvetica Neue", Arial;
  background: linear-gradient(180deg,#f7fbfc 0%, #ffffff 60%);
  color: #222;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* --- App container --- */
.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* --- NAVBAR --- */
.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  padding: 14px 18px;
  background: linear-gradient(90deg,#0f8f6b,#27ae60);
  color: #fff;
  box-shadow: 0 4px 18px rgba(15, 15, 15, 0.08);
  position: sticky;
  top: 0;
  z-index: 40;
}

.navbar h1 {
  margin: 0;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: .2px;
}

.nav-links {
  display: flex;
  gap: 8px;
  align-items: center;
}

.nav-links button {
  background: rgba(255,255,255,0.12);
  color: #fff;
  border: none;
  padding: 8px 14px;
  border-radius: 20px;
  cursor: pointer;
  font-weight: 600;
  transition: transform .14s ease, background .14s ease;
}
.nav-links button:hover { transform: translateY(-3px); background: rgba(255,255,255,0.18); }
.nav-links button.active { background: #fff; color: #0f8f6b; box-shadow: 0 6px 18px rgba(15, 143, 107, .12); }

/* --- PAGE CONTENT --- */
.page-content {
  padding: 20px;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  flex: 1;
}

/* --- SEARCH BAR (centered) --- */
.search-bar {
  display: block;
  width: 92%;
  max-width: 640px;
  margin: 12px auto;
  padding: 12px 16px;
  border-radius: 999px;
  border: 1px solid rgba(16, 24, 32, 0.08);
  background: linear-gradient(180deg,#fff,#fbfffc);
  box-shadow: 0 4px 18px rgba(16,24,32,0.04);
  font-size: 15px;
  outline: none;
  transition: box-shadow .18s ease, border-color .18s ease, transform .12s ease;
}
.search-bar:focus {
  border-color: #27ae60;
  box-shadow: 0 8px 28px rgba(39,174,96,0.12);
  transform: translateY(-2px);
}

/* --- REGION PICKER --- */
.region-buttons {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  justify-content: center;
  margin: 16px 0;
}
.region-buttons button {
  padding: 8px 14px;
  border-radius: 999px;
  border: 1px solid rgba(16,24,32,0.06);
  background: #ffffff;
  cursor: pointer;
  font-weight: 600;
  color: #333;
  transition: transform .12s ease, background .12s ease, box-shadow .12s ease;
}
.region-buttons button:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(12,20,30,0.06);
}
.region-buttons button.active {
  background: linear-gradient(90deg,#0f8f6b,#27ae60);
  color: #fff;
  border-color: rgba(255,255,255,0.06);
  box-shadow: 0 10px 22px rgba(15,143,107,0.12);
}

/* --- GRID / CARDS --- */
.animal-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  gap: 18px;
  margin-top: 18px;
}

/* Card (clickable) */
.animal-card {
  background: #fff;
  border-radius: 12px;
  overflow: hidden;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  transition: transform .16s ease, box-shadow .16s ease;
  box-shadow: 0 6px 18px rgba(12,20,30,0.04);
  border: 1px solid rgba(12,20,30,0.03);
}
.animal-card:focus, .animal-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 12px 36px rgba(12,20,30,0.12);
  outline: none;
}

/* image container keeps height consistent */
.animal-card .img-wrap {
  width: 100%;
  height: 140px;            /* smaller images */
  background: #eee;
  display: block;
  overflow: hidden;
  flex-shrink: 0;
}
.animal-card .img-wrap img {
  width: 100%;
  height: 100%;
  object-fit: cover;        /* crop nicely */
  display: block;
  transform-origin: center;
  transition: transform .5s ease;
}
.animal-card:hover .img-wrap img { transform: scale(1.06); }

/* content */
.animal-card .card-body {
  padding: 12px 14px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.animal-card h3 {
  margin: 0;
  font-size: 16px;
  line-height: 1.15;
  color: #0f2b1d;
}
.animal-card .meta {
  display: flex;
  justify-content: space-between;
  gap: 8px;
  font-size: 13px;
  color: #666;
  align-items: center;
}
/* Card text info (truncated after 3 lines) with vendor prefixes */
.animal-card .info {
  font-size: 13px;
  color: #444;
  line-height: 1.35;
  margin-top: 4px;
  
  display: -webkit-box;       /* Safari / Chrome */
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;      /* number of lines to show */

  display: box;               /* Old syntax fallback */
  box-orient: vertical;
  
  overflow: hidden;
  text-overflow: ellipsis;

  /* Modern standard syntax (Firefox and future browsers) */
  display: -webkit-box;
  -webkit-line-clamp: 3;
  line-clamp: 3;
  -o-text-overflow: ellipsis;
}


/* small badge for region */
.badge-region {
  font-size: 12px;
  padding: 6px 8px;
  border-radius: 999px;
  background: rgba(15,143,107,0.08);
  color: #0f8f6b;
  font-weight: 700;
}

/* --- Modal (in case you add it) --- */
.modal-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(5,10,12,0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  padding: 20px;
}
.modal-content {
  width: 100%;
  max-width: 760px;
  background: #fff;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 30px 80px rgba(10,20,30,0.4);
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.modal-left img { width: 100%; height: 100%; object-fit: cover; display: block; }
.modal-right { padding: 20px; text-align: left; }
.modal-close {
  position: absolute;
  right: 18px;
  top: 18px;
  background: #fff;
  border-radius: 8px;
  border: none;
  padding: 6px 10px;
  cursor: pointer;
  box-shadow: 0 6px 18px rgba(0,0,0,0.08);
}

/* --- About page --- */
.about-page {
  max-width: 900px;
  margin: 20px auto;
  background: #fff;
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 6px 24px rgba(12,20,30,0.04);
  text-align: left;
}

/* --- Small screens --- */
@media (max-width: 860px) {
  .modal-content { grid-template-columns: 1fr; }
  .animal-card .img-wrap { height: 140px; }
  .animal-grid { gap: 14px; grid-template-columns: repeat(auto-fill, minmax(180px, 1fr)); }
  .search-bar { width: 94%; }
}

@media (max-width: 480px) {
  .nav-links button { padding: 8px 10px; font-size: 14px; }
  .animal-card .img-wrap { height: 120px; }
  .animal-card h3 { font-size: 15px; }
  .animal-card .info { -webkit-line-clamp: 4; }
  .region-buttons { gap: 8px; }
}
